.oneline {
  display: inline-flex;
}

.oneline #startknop {
  margin: 5vh 5vw;
}

.quizvraag h1 {
  margin-top: 10vh;
}

.quizvraag h4 {
  width: 80%;
  margin: auto;
  margin-bottom: 3vh;
  font-size: 30px;
}

.antvraag {
  width: 20%;
  margin: auto;
}

.quizvraag h5 {
  width: 80%;
  margin: 5vh auto 2vh auto;
  font-size: 25px;
  color: #c0392b;
}

.inputantwoorden {
  display: flex;
  width: 80%;
  margin: auto;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 5vh;
  margin-bottom: 2vh;
}

.antwoordlijn {
  display: flex;
  width: auto;
  justify-content: center;
  margin: 5px 5vw;
}

.antwoord {
  float: left;
  margin: 0 50px;
  text-align: center;
}

.antwoordlijn select {
  margin: 0;
  width: 80px;
  font-size: 20px;
}
